<template>
	<div id="takeout-order">
		<public-header title="订单设置"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell>
					<div slot="title">允许顾客删除历史订单</div>
					<iswitch :value="order.customer_delete_order" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="order.customer_delete_order"></iswitch>
				</van-cell>
				<van-cell>
					<div slot="title">未支付订单在待接单列表展示</div>
					<iswitch :value="order.show_no_pay" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="order.show_no_pay"></iswitch>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">配送员接单后是否通知商户</div>
					<iswitch :value="order.deliveryer_collect_notify_clerk" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="order.deliveryer_collect_notify_clerk"></iswitch>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">允许商家接单后取消订单</div>
					<iswitch :value="order.cancel_after_handle" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="order.cancel_after_handle"></iswitch>
				</van-cell>
				<van-cell>
					<div slot="title">商家/系统取消订单后自动退款</div>
					<iswitch :value="order.auto_refund_cancel_order" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="order.auto_refund_cancel_order"></iswitch>
				</van-cell>
			</van-cell-group>
			<van-radio-group v-model="order.store_sailed_type" class="margin-10-t">
				<van-cell-group>
					<van-cell title="门店销量统计方式"></van-cell>
					<van-cell title="按订单数" clickable @click="order.store_sailed_type = 'order'" class="border-0px">
						<van-radio name="order" />
					</van-cell>
					<van-cell title="按商品数" clickable @click="order.store_sailed_type = 'goods'" class="border-0px">
						<van-radio name="goods" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<div class="padding-15">
				<van-button size="normal" class="bg-primary font-16" block @click="onSubmit">提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				order:{
					customer_delete_order: '1',
					show_no_pay: '1',
					deliveryer_collect_notify_clerk: '1',
					cancel_after_handle: '1',
					auto_refund_cancel_order: '1',
					store_sailed_type: 'order',
				}
			}
		},
		components: {
			publicHeader,
			iswitch
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					autoAssign: true,
					variable: 'order',
					url: 'plateform/config/takeout/order'
				});
			},

			jsToggleSwitch(res){
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			},

			onSubmit(){
				var that = this;
				setData({
					vue: this,
					url: 'plateform/config/takeout/order',
					data: that.order
				});
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	
</style>


